<template>
	<input
		type="text"
		placeholder="By country, city, capital etc.."
		v-model="searchInput"
	/>
	<button @click="searchQuery">Search</button>
</template>

<script>
	export default {
		data() {
			return {
				searchInput: ""
			};
		},
		methods: {
			searchQuery() {
				this.$emit("searchquery", this.searchInput);
			}
		},
		emits: ["searchquery"]
	};
</script>

<style scoped>
	input {
		border: 0;
		padding: 6px;
	}
</style>
