<template>
	<div class="img-container">
		<img :src="imageUrl" alt="Missing Image" :class="classObject" />
	</div>
	<section class="input-container">
		<input type="text" v-model="this.guessText" placeholder="Input..." />
		<button @click="validateGuess">Guess!</button>
	</section>
</template>

<script>
	const axios = require("axios");
	const correctSound = new Audio(require("../../public/correct.mp3"));
	const incorrectSound = new Audio(require("../../public/incorrect.mp3"));

	/* eslint-disable */
	export default {
		data() {
			return {
				imageUrl: "",
				pokemonList: [],
				pokemonLoaded: false,
				revealed: false,
				pokemonName: "",
				guess: false,
				guessText: ""
			};
		},
		methods: {
			getPokemonList() {
				axios.get("https://pokeapi.co/api/v2/pokemon?limit=150").then((res) => {
					this.pokemonList = res.data.results;
					this.pokemonLoaded = true;
				});
			},
			getPokemon() {
				if (this.pokemonLoaded === true) {
					const randomNumber = Math.floor(Math.random() * Math.floor(150));
					return this.pokemonList[randomNumber];
				} else {
					// console.log("Pokemon list not ready yet");
					return { name: "pikachu" };
				}
			},
			getNewPokemon() {
				// console.log("Getting new Pokemon...");

				const pokemon = this.getPokemon();
				this.pokemonName = pokemon.name;
				axios
					.get(`https://pokeapi.co/api/v2/pokemon/${this.pokemonName}`)
					.then((res) => {
						// console.log("Pokemon are loaded");
						this.imageUrl =
							res.data.sprites.other["official-artwork"]["front_default"];
						this.revealed = false;
					});
				//input validation
			},
			validateGuess() {
				//check if guess is correct
				if (this.guessText === this.pokemonName) {
					this.guess = true;
					correctSound.play();
				} else {
					this.guess = false;
					incorrectSound.play();
				}
				this.revealed = true;
				this.guessText = this.pokemonName;
				//fetch new pokemon
				setTimeout(this.getNewPokemon, 2500);
			}
		},
		computed: {
			classObject: function() {
				return {
					"revealed-correct": this.revealed && this.guess,
					"revealed-incorrect": this.revealed && !this.guess
				};
			}
		},
		mounted() {
			this.getPokemonList();
			this.getNewPokemon();
		}
	};
</script>

<style scoped>
	.img-container {
		position: absolute;
		width: auto;
		height: auto;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 1rem;
		left: 100px;
		z-index: 100;
	}

	.input-container {
		/* border: 1px solid black; */
		width: 12vw;
		position: absolute;
		bottom: 30px;
		left: 100px;
		display: flex;

		gap: 1rem;
	}

	img {
		height: 300px;
		width: 300px;
		filter: brightness(0%);
	}

	.revealed-correct {
		filter: brightness(100%);
		-webkit-filter: drop-shadow(2px 2px 0 green);
		filter: drop-shadow(2px 2px 0 green);
	}

	.revealed-incorrect {
		filter: brightness(100%);
		-webkit-filter: drop-shadow(2px 2px 0 red);
		filter: drop-shadow(2px 2px 0 red);
	}
</style>
